/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { Link, graphql } from 'gatsby';
import Img from 'gatsby-image';
import type { FluidObject } from 'gatsby-image';
import type { FC } from 'react';

import Layout from './layout';
import Seo from './seo';
import { rhythm } from './styles/typography';
import Pagination from './pagination';

type Node = {
  id: string;
  path: string;
  slug: string;
  title: string;
  author: string;
  image: { sharp: { fluid: FluidObject } };
  parent: { excerpt: string };
};

type DevlogListTemplateProps = {
  data: {
    allDevlog: { nodes: Node[] };
  };
  pageContext: {
    limit: number;
    skip: number;
    numPages: number;
    currentPage: number;
  };
};

const DevlogListTemplate: FC<DevlogListTemplateProps> = ({ data, pageContext }) => {
  const { nodes } = data.allDevlog;

  return (
    <Layout>
      <Seo title="Devlog" description="Project updates and articles about software development" />

      <nav
        css={css`
          margin: 1.5em 0;
        `}
      >
        <ul
          css={css`
            list-style: none;
            margin: 0;
            padding: 0;
          `}
        >
          <li>
            <Link
              css={css`
                color: hsla(0, 0%, 0%, 0.63);
                font-family: 'Recursive', sans-serif;
                user-select: none;
              `}
              to="/"
            >
              &larr; back to home
            </Link>
          </li>
        </ul>
      </nav>

      <h1>Devlog</h1>

      <div>
        {nodes.map(({ id, image, title, parent, path }) => (
          <article
            css={css`
              border-bottom: 1px solid #ddd;
              display: flex;
              align-items: flex-start;
              justify-content: space-between;
              margin-top: 0;
              padding: 1rem 0;

              &:last-child {
                border-bottom: none;
              }
            `}
            key={id}
          >
            <Link
              to={path}
              css={css`
                align-self: flex-start;
                min-width: 100px;
                display: none;

                @media screen and (min-width: 60em) {
                  & {
                    display: initial;
                  }
                }
              `}
            >
              <Img
                css={css`
                  border-radius: 3px;
                  pointer-events: none;
                  user-select: none;
                `}
                fluid={image.sharp.fluid}
                alt={title}
              />
            </Link>

            <div
              css={css`
                flex: 1;

                @media screen and (min-width: 60em) {
                  & {
                    flex: 0.97;
                  }
                }
              `}
            >
              <h3
                css={css`
                  margin: 0 0 ${rhythm(0.5)};
                `}
                title={title}
              >
                <Link to={path}>{title}</Link>
              </h3>

              <p
                css={css`
                  color: hsla(0, 0%, 0%, 0.63);
                  margin: 0;
                `}
              >
                {parent.excerpt}
              </p>
            </div>
          </article>
        ))}

        {pageContext.currentPage === pageContext.numPages ? (
          <div
            css={css`
              display: flex;
              font-family: 'Recursive', sans-serif;
              justify-content: center;
              padding: ${rhythm(0.7)} ${rhythm(1)};
              color: hsla(0, 0%, 0%, 0.63);
            `}
          >
            You&apos;ve reached the end.
          </div>
        ) : null}
      </div>

      {nodes.length >= pageContext.limit ? <Pagination prefix="devlog" {...pageContext} /> : null}
    </Layout>
  );
};

export const query = graphql`
  query($skip: Int!, $limit: Int!) {
    allDevlog(limit: $limit, skip: $skip, sort: { fields: date, order: DESC }) {
      nodes {
        id
        path
        slug
        title
        author
        image {
          sharp: childImageSharp {
            fluid(maxWidth: 100, maxHeight: 100) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        parent {
          ... on Mdx {
            excerpt(pruneLength: 80)
          }
        }
      }
    }
  }
`;

export default DevlogListTemplate;
