/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { Link } from 'gatsby';
import type { FC } from 'react';

import { rhythm } from './styles/typography';

type PaginationProps = {
  numPages: number;
  currentPage: number;
  prefix: string;
};

const Pagination: FC<PaginationProps> = ({ numPages, currentPage, prefix }) => {
  const isFirst = currentPage === 1;
  const isLast = currentPage === numPages;

  const nextPageNum = currentPage + 1;
  const prevPageNum = currentPage - 1 === 1 ? '' : currentPage - 1;

  const pathPrefix = prefix ? `/${prefix}/` : '/';
  const prevPageLink = isFirst ? null : `${pathPrefix}${prevPageNum}/`;
  const nextPageLink = isLast ? null : `${pathPrefix}${nextPageNum}/`;

  return (
    <div
      css={css`
        display: flex;
        justify-content: space-between;
        padding: ${rhythm(0.7)} 0;
        margin-bottom: ${rhythm(1)};

        svg {
          vertical-align: middle;
        }
      `}
    >
      {!isFirst && prevPageLink && (
        <Link
          css={css`
            order: 1;
            justify-self: flex-start;
          `}
          to={prevPageLink}
        >
          &larr; Previous
        </Link>
      )}
      {!isLast && nextPageLink && (
        <Link
          css={css`
            order: 2;
            justify-self: flex-end;
          `}
          to={nextPageLink}
        >
          Next &rarr;
        </Link>
      )}
    </div>
  );
};

export default Pagination;
